.button {
  border: none;
  margin-top: 30px;
  width: 200px;
  height: 34px;
  background: #5093e2;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  color: #ffffff;
  transition: 0.3s;
}
.button:hover {
  background: #1177eb;
}
