.text {
  margin-top: 33px;
  width: 247px;
  font-family: "Inter";
  font-style: normal;
  display: flex;
  flex-direction: column;
  row-gap: 27px;
}
.block__title {
  margin: 0 0 5px 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
  color: #f5f5f5;
}
.block__paragraph {
  margin: 0;
  font-weight: 400;
  font-size: 10.24px;
  line-height: 150%;
  color: #dcdcdc;
}
